@import "sass/index";

.invitesBox {
  margin-top: 50px;
  width: max-content;
  margin: 0 auto;
  position: relative;
}
.invitesTitle {
  margin: 0 auto;
  width: 160px;
  height: 70px;
  background: use(--primary-contrastText);
  border-radius: 50px;
  color: use(--primary-main);
  z-index: 1;
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
}
.invitesLogo {
  height: 32px;
  width: 32px;
}
.invitesTitleText {
  margin: 0 10px;
  font-size: 1.5rem;
  font-weight: bold;
}
.levelContentBox {
  padding: 15px 30px;
  background: use(--secondary-light);
  border-radius: 15px;
  margin: 0 auto;
  margin-top: 55px;
  width: max-content;
  z-index: 1;
  position: relative;

  & > * {
    display: block;
    margin: 0 auto;
    text-align: center;
  }
}
.levelText {
  margin: 10px auto;
}
.title {
  overflow-wrap: break-word;
}

.line {
  background: use(--secondary-light);
  height: 100%;
  width: 2px;
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 0;
}
.levelButton {
  width: 100%;
}
