@import "sass/index";

$black: #000;
$white: #fff;
$milk-gray: #bbbbbb;
$light-gray: #7e7a7d;
$middle-gray: #636265;
$gray: #515054;
$darken-gray: #3c3a3e;
$semi-dark-gray: #343138;
$dark-gray: #312f36;
$dark: #262429;
$cherry: #dd5389;
$green: #30cf8a;
$lime: #00ffc3;
$aqua: #00e7ff;
$black-violet: #150a30;
$dark-violet: #210551;
$darken-violet: #301b52;
$violet: #5e15e2;
$light-violet: #9f8dd0;

