@import "sass/index";

.root {
  height: 100%;
  background: use(--template-anyContent-background);
  border-radius: 10px;
  padding: 50px;
  width: 100%;
  max-width: 800px;
  position: relative;

  @media (max-width: 360px) {
    padding: 50px 20px 20px;
  }

  @media (max-width: 600px) {
    max-width: 100%;
    margin: 0;

    max-height: 100%;
  }
}
.closeIcon {
  position: absolute;
  top: 10px;
  right: 10px;

  & path {
    stroke: use(--template-modal-closeIcon-color);
  }
}
.titleBox {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  color: use(--template-anyContent-colors-first);
  margin-bottom: 100px;

  & > * {
    line-height: 1;
    width: min-content;
  }
}
.title {
  margin: 0;
  font-weight: bold;
  font-size: 1.5rem;
  margin-right: 10px;
  color: use(--template-anyContent-colors-first);
  line-height: 1.05;

  @media (max-width: 380px) {
    font-size: 1.2rem;
  }
}
.value {
  margin: 0 10px;
  font-size: 3.25rem;
  font-family: "Varela Round", sans-serif, sans-serif;
  color: use(--template-anyContent-colors-first);

  @media (max-width: 380px) {
    font-size: 2.5rem;
  }
}
.money {
  opacity: 0.5;
  font-size: 1.2rem;
  font-weight: bold;
  color: use(--template-anyContent-colors-first);

  @media (max-width: 380px) {
    font-size: 0.9rem;
  }
}

.content {
}

.valueWrapper {
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 2rem;
  position: relative;
  bottom: -8px;
  max-width: 75%;
}

.paginationBlock {
  width: 100%;
  height: 40px;

  display: flex;
  justify-content: flex-end;
  margin-bottom: 15px;
}

.paginationRoot {
}
.paginationButton {
  background: transparent !important;
  box-shadow: none;
}
.paginationActive {
  color: use(--template-anyContent-colors-first) !important;
  background: use(--template-anyContent-colors-second) !important;
}
