@import "sass/index";

.rewardField {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: use(--secondary-light);
  border-radius: 45px;
  padding: 16px 40px;
  margin: 10px 0;
  width: 100%;
  max-width: 500px;

  & * {
    margin: 0;
  }

  @media (max-width: 540px) {
    padding: 10px 20px;
  }
}
.smallText {
  font-family: "Varela Round", sans-serif;
  overflow-wrap: break-word;
  font-weight: 400;
  color: use(--primary-contrastText, 0.5);
  font-size: 0.9rem;
  margin-left: 10px;
  text-align: center;

  & > span {
    font-size: 1.1rem;
    color: --primary-contrastText;
  }
}

.wrapper {
  @media (max-width: 370px) {
    margin: 0;
  }
}
