@import "sass/index";

.wrapper {
  max-width: 100%;
  overflow: auto;
  height: 100%;
  background: use(--template-anyContent-background);
  border-radius: 10px;
  padding: 25px;

  @media (max-width: 400px) {
    padding: 25px 10px;
  }
}
.loader {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  & > div {
    height: 75px !important;
    width: 75px !important;
  }
}

.title {
  margin: 0 0 10px 0;
  font-size: 2.125rem;
  color: use(--template-anyContent-colors-first);
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: bold;

  @media (max-width: 360px) {
    font-size: 1.8rem;
  }
}
.text {
  font-size: 1.25rem;
  line-height: 1.3rem;
  font-weight: 400;
  font-family: "Varela Round", sans-serif, sans-serif;
  color: use(--template-anyContent-colors-first);
  opacity: 0.5;

  @media (max-width: 360px) {
    font-size: 1rem;
  }
}
.titleWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 50px;
  padding-bottom: 40px;
  font-weight: wrap;

  border-bottom: 1px solid use(--secondary-main, 0.3);

  @media (max-width: 1080px) {
    margin: 20px;
  }

  @media (max-width: 450px) {
    margin: 20px 0;
  }
}
.titleContent {
  margin-right: 5px;
  min-width: 275px;
  max-width: 320px;
}
.buttonContent {
  margin-left: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 260px;
}

.button {
  margin: 0 10px 10px 0;
  font-size: 0.9rem;
  font-family: "Varela Round", sans-serif, sans-serif;
  text-transform: none;
  font-weight: 400;
  border-radius: 5px;
  color: use(--template-anyContent-background);
}
.mainButton {
  background: use(--template-anyContent-colors-first) !important;
}

.subWrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 20px 0;
}

.subContent {
  & > * {
    display: block;
    margin: 0;
    line-height: 120%;
  }
}
.subTitle {
  font-size: 1.5rem;
  color: use(--template-anyContent-colors-first);

  @media (max-width: 360px) {
    font-size: 1.2rem;
  }
}
.subNumber {
  height: 40px;
  width: 40px;
  min-height: 40px;
  min-width: 40px;
  background: use(--template-anyContent-colors-first);
  color: use(--template-anyContent-background);
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
  font-family: "Varela Round", sans-serif, sans-serif;
  margin-right: 10px;
}

.part {
  display: block;
  margin: 50px;
  max-width: 100%;
  overflow: auto;

  @media (max-width: 1080px) {
    margin: 20px;
    margin-top: 50px;
  }

  @media (max-width: 450px) {
    margin: 20px 0;
    margin-top: 50px;
  }
}
.content {
  margin: 0 30px 0 50px;

  @media (max-width: 490px) {
    margin: 0 30px;
  }

  @media (max-width: 360px) {
    // margin: 0;
  }
}

.primaryButtonWrapper {
  width: 100%;
}
