@import "sass/index";

.root {
  margin: 20px 10px;

  & > div::-webkit-scrollbar {
    display: none;
  }
}
.info {
  & > :first-child {
    margin-bottom: 20px;
  }
}
.tableWrapper {
  overflow-x: auto;
}
.tableContainer {
  position: relative;
}
.table {
  border-radius: 15px;
  overflow: hidden;
}
.tHead,
.tBody,
.tCell {
  & p {
    @media (max-width: 600px) {
      font-size: 0.75rem;
    }
  }
}

.tHead {
  background: use(--template-anyContent-colors-third);
  border: none;

  color: use(--template-anyContent-colors-fourth);
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 14px;
  letter-spacing: 1px;
  text-align: center;
  line-height: 15px;
}
.tCell {
  white-space: nowrap;
  padding: 15px;
  border: none;

  @media (max-width: 0.6px) {
    padding: 10px 15px;
  }
}

.tContent {
  background: use(--template-anyContent-colors-five);
  color: use(--template-anyContent-colors-fourth);

  border: none;
  border-top: 1px solid use(--secondary-light);
  white-space: nowrap;
  text-align: center;
}

.tBody {
  & p {
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 16px;
    color: use(--template-statisticsBlock-tab-tableBody);
    letter-spacing: 0;
    text-align: center;
    line-height: 15px;
  }

  @media (max-width: $mobileScreen) {
    & p {
      font-family: "Gilroy";
      font-weight: 600;
      color: use(--template-statisticsBlock-tab-mobile-tableBody);

      @media (max-width: 600px) {
        font-size: 0.75rem;
      }
    }
  }
}

.lose {
  color: use(--subsidiary-red) !important;
}
.win {
  color: use(--subsidiary-green) !important;
}
.coinWrapper {
  position: relative;
}
