@import "sass/index";

.copyInput {
  max-width: 500px;
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
}
.input {
  margin: 0;
  background: use(--secondary-light);
  border: none;
  color: use(--primary-contrastText);
}
.icon {
  padding: 13px;

  @media (max-width: 360px) {
    margin: 5px;
    font-size: 0.9rem;
    padding: 8px 10px;
    width: 100%;
  }
}
.iconText {
  margin: 0 12px;
}

.flexContent {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  max-width: 515px;
  width: 100%;
  margin-top: 10px;

  & > * {
    margin: 5px 10px 5px 0;
  }
}
