@import "sass/index";

.activeText {
  font-family: "Varela Round", sans-serif;
  font-size: 0.8125rem;
  line-height: 16px;
  color: use(--primary-light);
  opacity: 0.5;

  width: 100%;
  text-align: center;
  margin: 20px 0;
}

.button {
  display: block;

  background: transparent !important;
  border: 2px solid #4323bd;

  color: use(--template-anyContent-colors-first);
  font-family: "Gilroy";
  line-height: 24px;
  font-size: 1.25rem;
  width: 100%;
  padding: 20px;

  max-width: 300px;
  border-radius: 8px;

  overflow: hidden;
  max-height: 68px;
}

.buttons {
  display: flex;
  justify-content: space-between;
  & > button:first-child {
    margin-right: 10px;
  }
}

.loader {
  margin-top: -12px;
}
