@import "sass/index";

.root {
  margin: 20px 10px;

  & > div::-webkit-scrollbar {
    display: none;
  }

  @media (max-width: 380px) {
    margin: 20px 0;
  }
}

.tableContainer {
  position: relative;
  overflow-x: visible;
  max-width: 400;
  margin: 0 auto;

  & table {
    border-radius: 15px;
    overflow: hidden;
  }
}
.tBody,
.tHead {
  & * {
    @media (max-width: 600px) {
      font-size: 0.75rem;
    }
  }
}
.tHead {
  background: use(--template-anyContent-colors-third);
  border: none;
}
.tCell {
  padding: 15px;
  border: none;

  color: use(--template-anyContent-colors-fourth);
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  font-size: 14px;
  letter-spacing: 1px;
  text-align: center;
  line-height: 15px;

  @media (max-width: 600px) {
    padding: 10px 15px;
  }
}

.tContent {
  background: use(--template-anyContent-colors-five);
  color: use(--template-anyContent-colors-fourth);

  border: none;
  border-top: 1px solid use(--template-anyContent-colors-third);
  white-space: nowrap;
  text-align: center;
}

.contentWrapper {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
}

.coinImg {
  position: static;
  margin-right: 5px;
}

.tBody {
  & * {
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 16px;
    color: use(--template-statisticsBlock-tab-tableBody);
    letter-spacing: 0;
    text-align: center;
    line-height: 15px;
  }

  @media (max-width: $mobileScreen) {
    & * {
      font-family: "Gilroy", sans-serif;
      font-weight: 600;
      color: --template-statisticsBlock-tab-mobile-tableBody;
    }
  }
}

.alert {
  color: use(--template-anyContent-colors-fourth);
}
