@import "sass/index";

.wrapper {
  height: 100%;
  background: use(--template-anyContent-background);
  border-radius: 10px;
  overflow-x: hidden;
  width: 100%;

  @media (max-width: 1300px) {
    max-width: 100%;
  }
}
.paddingBox {
  margin: 50px 20px 50px 50px;

  @media (max-width: 420px) {
    margin: 50px 20px 50px 20px;
  }
}
.loader {
  width: 100%;
  margin: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;

  & > div {
    height: 75px !important;
    width: 75px !important;
  }
}
.titleBox {
  color: use(--template-anyContent-colors-second);

  display: flex;
  flex-wrap: wrap;
}
.title {
  margin: 0;
  font-weight: bold;
  font-size: 1.5rem;
  margin-right: 10px;
  color: use(--template-anyContent-colors-second);
  line-height: 1.05;
  grid-area: "title";

  @media (max-width: 380px) {
    font-size: 1.2rem;
  }
}
.value {
  font-size: 3.25rem;
  font-family: "Varela Round", sans-serif;
  color: use(--template-anyContent-colors-second);
  margin-right: 10px;

  @media (max-width: 1460px) {
    font-size: 3rem;
  }

  @media (max-width: 380px) {
    font-size: 2.5rem;
  }
}
.money {
  opacity: 0.5;
  font-size: 1.2rem;
  font-weight: bold;
  color: use(--template-anyContent-colors-second);
  grid-area: "money";
  margin-left: 5px;

  @media (max-width: 380px) {
    font-size: 0.9rem;
  }
}

.content {
  margin-top: 100px;
}

.valueWrapper {
  max-width: 200px;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 1.8rem;
  position: relative;
  bottom: -8px;
  grid-area: "value";
}

.moneyWrapper {
  max-width: 100%;
  display: flex;
  align-items: flex-end;
}

.activityWrapper {
  overflow-x: auto;

  margin: 0 auto;
}
